<template>
  <div id="take-photo-button">上传照片</div>
  <div id="latlon">获取位置</div>
  <div id="map"></div>
  <div id="exif" @click="getExif">{{ exifdata }}</div>
  <img id="selectedImg" :src="imgsrc" />
</template>

<script>
import wx from "weixin-js-sdk";
import getSign from "../../generateSign";
import L from "leaflet";
import "leaflet/dist/leaflet.css";
import "leaflet.locatecontrol/dist/L.Control.Locate.min.css";
import "leaflet.locatecontrol/src/L.Control.Locate.js";
import EXIF from "exif-js";
export default {
  name: "HelloWorld",
  props: {
    msg: String,
  },
  data() {
    return {
      exifdata: "exxif",
      imgsrc:
        "data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABkAAAAZCAYAAADE6YVjAAAAJUlEQVR42u3NQQEAAAQEsJNcdFLw2gqsMukcK4lEIpFIJBLJS7KG6yVo40DbTgAAAABJRU5ErkJggg==",
    };
  },
  mounted() {
    //wx jssdk config
    var ticket =
      "O3SMpm8bG7kJnF36aXbe89Qd_lkJnRc33MsTutOp50HXDyjcBrZgFe5RdzmzKGBo17PSyyzZ2S24Sxnb_Kdh1A"; // 替换为你的ticket
    var url = "https://spacecadet.cc/"; // 替换为你的H5页面的URL
    var signature = getSign.getSign(ticket, url);
    wx.config({
      // 配置你的微信JSSDK参数
      debug: true,
      appId: "wxf0e097d5cf67ea39",
      timestamp: signature.timestamp,
      nonceStr: signature.nonceStr,
      signature: signature.signature,
      jsApiList: [
        "chooseImage",
        "getLocation",
        "uploadImage",
        "getLocalImgData",
      ], // 需要使用的API列表
    });
    var map = L.map("map", {
      center: [23, 114],
      zoom: 13,
    });
    L.control
      .locate({
        strings: {
          title: "Show me where I am, yo!",
        },
      })
      .addTo(map);
    var marker = null;
    const mapurl1 =
      "https://t3.tianditu.gov.cn/DataServer?T=cva_w&x={x}&y={y}&l={z}&tk=52eb0376ca0a56c6f6ed72ac8302a703";
    const mapurl2 =
      "https://t3.tianditu.gov.cn/DataServer?T=vec_w&x={x}&y={y}&l={z}&tk=52eb0376ca0a56c6f6ed72ac8302a703";
    L.tileLayer(mapurl2, {}).addTo(map);
    L.tileLayer(mapurl1, {}).addTo(map);
    let that = this;
    //sdk加载完成，ready事件
    wx.ready(function () {
      document
        .getElementById("take-photo-button")
        .addEventListener("click", function () {
          wx.chooseImage({
            count: 1, // 可选择的图片数量
            success: function (res) {
              var localId = res.localIds[0];
              //wx.uploadImage({
              //  localId: localId, // 需要上传的图片的本地ID，由chooseImage接口获得
              //  isShowProgressTips: 1, // 默认为1，显示进度提示
              //  success: function (res) {
              //    var serverId = res.serverId; // 返回图片的服务器端ID
              //    alert(serverId)
              //  },
              //});
              wx.getLocalImgData({
                localId: localId, // 图片的localID
                success: function (res) {
                  var localData = res.localData; // localData是图片的base64数据，可以用img标签显示
                  that.imgsrc = "data:image/png;base64," + localData;
                },
              });
            },
            fail: function (res) {
              alert(1);
            },
          });
        });
      document.getElementById("latlon").addEventListener("click", function () {
        wx.getLocation({
          type: "wgs84", // 默认为wgs84的gps坐标，如果要返回直接给openLocation用的火星坐标，可传入'gcj02',
          highAccuracyExpireTime: 8000,
          success: function (res) {
            var latitude = res.latitude; // 纬度，浮点数，范围为90 ~ -90
            var longitude = res.longitude; // 经度，浮点数，范围为180 ~ -180。
            var speed = res.speed; // 速度，以米/每秒计
            var accuracy = res.accuracy; // 位置精度
            marker = L.circleMarker(new L.latLng(res.latitude, res.longitude), {
              radius: 5,
            }).addTo(map);
          },
          fail: function (res) {
            alert(1);
          },
        });
      });
    });
  },
  methods: {
    getExif() {
      let _this = this
      var img1 = document.getElementById("selectedImg");
      EXIF.getData(img1, function () {
        alert(img1)
        var exifData = EXIF.getAllTags(this);
        _this.exifdata = exifData.FileType; 
      });
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#map {
  height: 300px;
  width: 300px;
}
</style>
