<template>
  <div>
    <img id="testimg" :src="base64src"/>
  </div>
</template>

<script>
import EXIF from 'exif-js'
export default {
    name: "exif",
    data(){
        return {
            base64src: "data:image/jpeg;base64,"
        }
    },
    mounted(){
        let _this = this
        var img1 = document.getElementById("testimg");
        EXIF.getData(img1, function () {
                    var exifData = EXIF.getAllTags(this);
                    console.log(exifData.DateTime)
                  });
    }
}
</script>

